import React from 'react'
import Person from 'components/person'
import { initialChatUsersStore } from '../'
import {
    Wrapper,
    Flex,
} from 'styles'

const Chats = () => {

    return (
        <Wrapper padding="5px" margin="0px" display="flex" direction="column" bg="#fff">
            <Flex direction="column">
                {
                    initialChatUsersStore.map(i => {
                        return <Person key={i.id} data={i} />
                    })
                }
            </Flex>
        </Wrapper>
    )
}

export default Chats



