import styled from 'styled-components'

const Container = styled.div`
    margin: 5px;
    padding: 5px;
    border-radius: var(--bradius);
    font-size: 15px;
    display: inline-flex;
    min-width: 300px;
    transition: .5s;
    @media (max-width: 500px) {
        min-width: inherit;
    }
    &:hover {
        box-shadow: inset 0 0 0 2px currentColor;
        transition: 0s;
    }
`

const OuterWrapper = styled.a`
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: .3s;
    width: calc(100% - 50px);
`

const Title = styled.span`
    font-family: var(--font-medium);
    padding: 0px 10px 10px;
    font-size: 16px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: calc(100% - 45px);
`

const Time = styled.span`
    margin-left: auto;
    font-size: 12px;
    padding: 0px 5px;
    color: #999999;
    opacity: .7;
`

const Answer = styled.span`
    padding: 5px 5px 0px 5px;
`

const ProfileIcon = styled.a`
    width: 50px;
    height: 50px;
    & img {
        border-radius: 50%;
        width: inherit;
        height: inherit;
        object-fit: cover;
        pointer-events: none;
    }
    &:hover {
        transform: scale(.9);
    }
`

const Message = styled.div`
    font-family: var(--font);
    display: flex;
    align-items: center;
    padding: 0px 5px;
    text-align: right;
    color: #999999;
    margin-left: auto;
    max-width: 85%;
`

const Text = styled.div`
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 100%;
    font-size: 15px;
    padding-right: 5px;
    opacity: 1;
    color: ${props => props.checked ? '#999999' : '#5DA3C0'};
`

const Checked = styled.div`
    display: flex;
    align-items: center;
    & img {
        pointer-events: none;
        height: 12px;
    }
`

export {
    Container,
    OuterWrapper,
    Time,
    Answer,
    Title,
    ProfileIcon,
    Message,
    Text,
    Checked
}
