import React from 'react'
import checkedMessageIcon from 'assets/img/checked-message.svg'
import checkedMessageTwiceIcon from 'assets/img/checked-message-twice.svg'
import {
    Container,
    OuterWrapper,
    ProfileIcon,
    Time,
    Title,
    Message,
    Text,
    Checked
} from './styles'
import {
    Wrapper
} from 'styles'

const Person = (
    {
        data: {
            image, userName, message, status, date
        },
        className="main"
    }) => {

    return (
        <Container className={className}>
            <ProfileIcon href="#">
                <img loading="lazy" src={image} alt="icon" />
            </ProfileIcon>
            <OuterWrapper href="#">
                <Wrapper justify="space-between" shadow="none" padding="0px" margin="0px">
                    <Title className={className}>{userName}</Title>
                    <Time>12:34</Time>
                </Wrapper>
                <Wrapper justify="space-between" shadow="none" padding="0px" margin="0px 0px 0px auto">
                    <Message>
                        <Text checked={status === 'readen'}>{message}</Text>
                        <Checked>
                            {status === 'readen' ?
                                <img loading="lazy" src={checkedMessageIcon} alt="status" />
                            :
                                <img loading="lazy" src={checkedMessageTwiceIcon} alt="status" />
                            }
                        </Checked>
                    </Message>
                </Wrapper>
            </OuterWrapper>
        </Container>
    )
}

export default Person
